.loadMore {
  margin-top: 26px;
  margin-bottom: 25px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 200px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  padding: 14px 28px;

  background: #6e6969;
  box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.25);
  border-radius: 10.3108px;
  border: none;
  transition: scale 400ms cubic-bezier(0.075, 0.82, 0.165, 1);
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  text-transform: uppercase;

  color: #383535;
  cursor: pointer;
}
.loadMore:hover {
  scale: 1.1;
}
.loadMore:active {
  background: #dff2ae;
}
